iframe {
  min-height: 450px;
}

@media (max-width:  760px) {
  iframe {
    min-height: 350px;
  }
}

#root {
  background-color: rgb(24 24 24);
  height: 100%;
  padding-bottom: 150px;
}

#navbar {
  background-color: rgb(32 32 32) !important;
}

.fas, .fab {
  color:  white;
  font-size: 18px;
}
.fab.fa-youtube {
  color:  red !important;
}

.video-result-card-channel {
  font-size:  12px;
}

.video-result {
  height: 100px;
  background-color: rgb(24 24 24);
  border: 0;
}

.video-result-card {
  padding: 0.5rem 1rem 0.5rem 0.5rem;
}

.video-result-card-title {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  color: white;
}

.video-result-title {
  color: white;
}

.video-detail {
  background-color:  rgb(24 24 24);
}
